@charset 'UTF-8';

$body-background: $color-blue;

body {
  background-color: $body-background;
}

.full-height {
  min-height: 100vh;
}

.page-title {
  font-size: 3.0rem;
  line-height: 1.1;
  font-weight: 300;

  @include fadeIn(
    $delay: 400ms,
    $duration: 300ms
  );

  h1 {
    background-image: url('../images/name.svg');
    background-position: left center;
    background-repeat: no-repeat;
    background-size: contain;
    font-size: 3rem;
    line-height: 3.5;
    margin: 1rem 0 .3rem 0;
    text-transform: lowercase;
    text-indent: -1000rem;
    white-space: nowrap;

    @include bounceInLeft(
      $delay: 400ms,
      $duration: 700ms
    );
  }

  @media (max-width: 930px) {
    h1 {
      line-height: 2.0;
    }
  }

  @media (orientation: portrait) {
    h1 {
      font-size: 3rem;
      line-height: 2;
    }
  }

  span {
    text-shadow: 3px 3px 0px rgba(0, 0, 0, .5);
  }
}

em {
  font-style: normal;
}

.layer {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  background-attachment: fixed;
}

.moon {
  background-image: url('../images/moon.svg');
  background-position: 50% 5%;
  background-repeat: no-repeat;
  background-size: 24vh;

  @include slideInDown(
    $delay: 100ms,
    $duration: 500ms
  );

  @media (max-width: 425px) {
    .moon { display: none }
  }
}

.majortom {
  background-image: url('../images/major.tom.svg');
  background-position: 89.4% 52%;
  background-repeat: no-repeat;
  background-size: 35vh;

  @include slideInUp(
    $duration: 400ms,
    $delay: 160ms,
    $function: ease-out
  );
}

.emissions {
  background-image: url('../images/emissions.svg');
  background-position: 90% 100%;
  background-repeat: no-repeat;
  background-size: 68vh;

  @include slideInUp(
    $duration: 500ms,
    $function: ease-out
  );
}

.scroll-arrow {
  height: 56px;
  width: 56px;
  bottom: -25px;
  left: 50%;
  margin: 0 0 50px -28px;
  background: rgba(34,34,34,0.35);
  visibility: visible;
  position: fixed;
  z-index: 10;
  cursor: pointer;
  background-size: 24px;
  border-radius: 100%;
  transition: all .15s ease-out;
}

.scroll-arrow img {
  height: 24px;
  width: 24px;
  top: 50%;
  left: 50%;
  margin: -12px;
  position: absolute;
}

.scroll-arrow:hover {
  height: 70px;
  width: 70px;
  margin: 0 0 38px -35px;
  background: rgba(34,34,34,0.5);
}

.scroll-arrow-container {
  height: 400%;
  width: 100%;
  position: absolute;
  top: 100%;
}

@media (max-height: 510px) and (min-width: 425px) {
  .scroll-arrow {
      display: none;
  }
}
